.alpheios-al-editor-segment-cell-origin-row {
  position: relative;
  padding: 10px 5px;
  overflow-y: auto;
}
.alpheios-al-editor-segment-cell-origin-row.alpheios-align-text-segment-origin-0 {
  padding-top: 30px;
}
.alpheios-al-editor-segment-cell-origin-row.alpheios-al-editor-segment-block-text__no-langname {
  padding: 10px;
}
.alpheios-al-editor-segment-cell-origin-row .alpheios-al-editor-segment-block-text__langname {
  position: absolute;
  overflow-y: hidden;
  border-bottom: 0;
  top: 0;
  right: 0;
  left: 0;
  margin: 0;
  padding: 0;
  text-align: right;
  padding: 5px;
  font-size: 90%;
  background: #185F6D;
  color: #fff;
  z-index: 100;
}

.alpheios-al-editor-segment-cell-target-row.alpheios-align-text-segment-target-0.alpheios-al-editor-segment-cell-target-row {
  padding-top: 30px;
}