.alpheios-al-editor-container-inner span.alpheios-token {
  cursor: pointer;
  padding: 2px 2px 0 2px;
  margin-bottom: 2px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-al-editor-container-inner span.alpheios-token .alpheios-token-inner {
  border: 1px solid transparent;
  display: inline-block;
  text-align: left;
}
.alpheios-al-editor-container-inner span.alpheios-token.alpheios-token-annotated .alpheios-token-inner {
  border-bottom: 1px solid;
}
.alpheios-al-editor-container-inner span.alpheios-token .alpheios-token-translation {
  display: block;
  text-align: left;
}
.alpheios-al-editor-container-inner span.alpheios-token .alpheios-token-translation {
  color: #9a9a9a;
  font-size: 95%;
}