.alpheios-al-editor-container-inner {
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  background: #F8F8F8;
  color: #ae0000;
}
.alpheios-al-editor-container-inner .alpheios-al-editor-segment-row {
  background: #F8F8F8;
  border-bottom: 2px solid #ddd;
}
.alpheios-al-editor-container-inner .alpheios-al-editor-segment-row.alpheios-al-editor-segment-row-last {
  border-bottom-color: transparent;
}
.alpheios-al-editor-container-inner .alpheios-al-editor-segment-cell-target-row {
  padding: 10px;
  max-height: 400px;
  overflow-y: scroll;
  /*
  &.alpheios-align-text-segment-origin,
  &.alpheios-align-text-segment-target-last {
    border-bottom: 2px solid  transparent;
  }
  */
}
.alpheios-al-editor-container-inner .alpheios-al-editor-segment-cell.alpheios-al-editor-segment-cell-target {
  border-left: 2px solid #ddd;
}

.alpheios-al-editor-view-full .alpheios-al-editor-container-inner .alpheios-al-editor-segment-row {
  display: table;
  width: 100%;
}
.alpheios-al-editor-view-full .alpheios-al-editor-container-inner .alpheios-al-editor-segment-cell {
  display: table-cell;
  width: 50%;
  vertical-align: top;
}

.alpheios-al-editor-container-inner .alpheios-al-editor-segment-cell-target-row {
  position: relative;
}