





































































































































































.alpheios-header {
  display: flex;
  justify-content: space-between;
}
.alpheios-alignment-editor-container {
    padding: 15px;
    height: calc(100% - 110px);
}

button.alpheios-actions-menu-button.alpheios-actions-menu-button-with-icon {
  padding: 5px;
  margin: 10px 5px 0;
  .alpheios-alignment-button-icon {
      display: inline-block;
      width: 20px;
      height: 20px;

      svg {
        width: 100%;
        height: 100%;
        display: block;
        fill: #fff;
      }
  }
}
.alpheios-alignment-app-menu-open-icon {
    display: block;
    position: fixed;
    top: 25px;
    left: 15px;
    width: 25px;
    height: 25px;
    cursor: pointer;


    svg {
      width: 100%;
      height: 100%;
      display: block;
    }
}

.alpheios-alignment-editor-container__view-notice {
  text-align: right;
  margin-top: 0;
  font-size: 90%;
  color: #808080;

  b {
    font-size: 100%;
    color: #000;
  }
}

.alpheios-alignment-editor-container-top-line {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  .alpheios-alignment-editor-container-question-button {
    .alpheios-actions-menu-button {
      margin: 2px 0 0 40px;
    }
  }
}

