














































































.alpheios-alignment-header-line {
  padding: 60px 0 10px;
  &.alpheios-alignment-header-line-in-header {
    padding-top: 0;
  }
}

.alpheios-alignment-radio-block {
  // margin: 0 10px 10px;
  span {
      display: block;
      // margin-right: 20px;
      // margin-bottom: 5px;
      background: #fff;
      border-bottom: 1px solid #e0e0e0;
      padding: 5px;
  }

  input.alpheios-alignment-input__sentence-count {
    width: 50px;
  }
}

.alpheios-alignment-header-line-in-header {
  .alpheios-alignment-radio-block {
    text-align: right;
    span {
      display: inline-block;
      margin-right: 20px;
      margin-bottom: 5px;
      background: transparent;
      border-bottom: none;
      padding: 0;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}
