.alpheios-al-editor-view-columns .alpheios-al-editor-container-inner {
  margin-top: 15px;
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  background: #F8F8F8;
  color: #ae0000;
}
.alpheios-al-editor-view-columns .alpheios-al-editor-container-inner .alpheios-al-editor-segment-row {
  background: #F8F8F8;
  border-bottom: 2px solid #185F6D;
}
.alpheios-al-editor-view-columns .alpheios-al-editor-container-inner .alpheios-al-editor-segment-cell.alpheios-al-editor-segment-cell-target {
  border-left: 2px solid #ddd;
}

.alpheios-al-editor-container-inner .alpheios-al-editor-segment-cell-target-row {
  padding: 10px 5px;
  max-height: 400px;
  overflow-y: auto;
}

.alpheios-al-editor-view-columns .alpheios-al-editor-container-inner .alpheios-al-editor-segment-row {
  width: 100%;
  display: table;
}
.alpheios-al-editor-view-columns .alpheios-al-editor-container-inner .alpheios-al-editor-segment-row .alpheios-al-editor-segment-row-cell {
  display: table-cell;
  vertical-align: top;
  width: 33.33%;
  border-right: 2px solid #d8d8d8;
}