.alpheios-alignment-editor-modal-help .alpheios-modal-container {
  width: 700px;
}
.alpheios-alignment-editor-modal-help .alpheios-modal-body {
  border: 0;
  padding: 0 15px 0 0;
  margin: 0 0 20px;
}
.alpheios-alignment-editor-modal-help .alpheios-alignment-editor-modal-header {
  text-transform: uppercase;
  margin-bottom: 10px;
}
.alpheios-alignment-editor-modal-help .alpheios-editor-help-content {
  text-indent: 10px;
  text-align: justify;
}
.alpheios-alignment-editor-modal-help .alpheios-editor-help-content .alpheios-editor-help-content-error {
  color: #f00;
  font-weight: bold;
}