









































.alpheios-alignment-editor-modal-help {
  
  .alpheios-modal-container {
    width: 700px;
  }
  .alpheios-modal-body {
    // max-height: 700px;
    border: 0;
    padding: 0 15px 0 0;
    margin: 0 0 20px;
  }
  .alpheios-alignment-editor-modal-header {
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  .alpheios-editor-help-content {
    text-indent: 10px;
    text-align: justify;
    

    .alpheios-editor-help-content-error {
        color: #f00;
        font-weight: bold;
    }
  }
}
