.alpheios-alignment-app-menu {
  height: 100%;
  width: 280px;
  position: fixed;
  z-index: 10000;
  top: 0;
  left: -280px;
  background: rgb(224, 224, 224);
  background: -moz-linear-gradient(180deg, rgb(224, 224, 224) 0%, rgba(255, 255, 255, 0.5) 100%);
  background: -webkit-linear-gradient(180deg, rgb(224, 224, 224) 0%, rgba(255, 255, 255, 0.5) 100%);
  background: linear-gradient(180deg, rgb(224, 224, 224) 0%, rgba(255, 255, 255, 0.5) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#e0e0e0",endColorstr="#ffffff",GradientType=1);
  overflow-x: hidden;
  transition: left 0.5s;
}
.alpheios-alignment-app-menu.alpheios-shown {
  left: 0;
}
.alpheios-alignment-app-menu .alpheios-alignment-app-menu-close-icon {
  display: block;
  position: absolute;
  top: 20px;
  right: 25px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.alpheios-alignment-app-menu .alpheios-alignment-app-menu-close-icon svg {
  width: 100%;
  height: 100%;
  display: block;
}

.alpheios-alignment-app-menu__buttons {
  padding: 20px 20px 10px;
}
.alpheios-alignment-app-menu__buttons.alpheios-alignment-menu-only-filter {
  padding-top: 60px;
}