.alpheios-al-editor-languages-list-item {
  display: inline-block;
  border: 1px solid #ddd;
  background: #fff;
  padding: 5px 10px;
}
.alpheios-al-editor-languages-list-item.alpheios-al-editor-languages-list-item-clickable {
  cursor: pointer;
}

.alpheios-icon-button {
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  padding: 0 5px;
}

.alpheios-al-editor-languages-block-horizontal .alpheios-al-editor-languages-list-item {
  display: block;
}