.alpheios-alignment-header-line {
  padding: 60px 0 10px;
}
.alpheios-alignment-header-line.alpheios-alignment-header-line-in-header {
  padding-top: 0;
}

.alpheios-alignment-radio-block span {
  display: block;
  background: #fff;
  border-bottom: 1px solid #e0e0e0;
  padding: 5px;
}
.alpheios-alignment-radio-block input.alpheios-alignment-input__sentence-count {
  width: 50px;
}

.alpheios-alignment-header-line-in-header .alpheios-alignment-radio-block {
  text-align: right;
}
.alpheios-alignment-header-line-in-header .alpheios-alignment-radio-block span {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 5px;
  background: transparent;
  border-bottom: none;
  padding: 0;
}
.alpheios-alignment-header-line-in-header .alpheios-alignment-radio-block span:last-of-type {
  margin-right: 0;
}