.alpheios-al-editor-segment-cell-target-row__bar {
  margin: 0;
  display: inline-block;
  position: absolute;
  min-height: 2px;
  overflow-y: hidden;
  border-bottom: 0;
  top: 0;
  right: 0;
  left: 0;
  margin: 0;
  padding: 0;
  padding: 0px;
  font-size: 90%;
  background: #185F6D;
  color: #fff;
  z-index: 100;
}
.alpheios-al-editor-segment-cell-target-row__bar .alpheios-al-editor-segment-cell-target-row__metadata {
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: calc(100% - 210px);
  cursor: pointer;
  text-align: left;
  vertical-align: top;
  overflow: hidden;
  padding: 5px 10px;
}
.alpheios-al-editor-segment-cell-target-row__bar .alpheios-al-editor-segment-cell-target-row__metadata.alpheios-al-editor-segment-cell-target-row__metadata-full {
  white-space: initial;
}
.alpheios-al-editor-segment-cell-target-row__bar .alpheios-al-editor-segment-cell-target-row__langname {
  display: inline-block;
  width: 170px;
  text-align: right;
  vertical-align: top;
  padding: 5px;
}